$background: #6383ce;
$base: #1b2949;
$second: #445e9a;
.scene-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .scene {
    width: 8rem;
    height: 8rem;
    perspective: 24rem;
    .cube-left {
      transform: rotateY(90deg) rotateX(0deg);
    }
    .cube-back {
      transform: rotateY(180deg) rotateX(0deg);
    }
    .cube-right {
      transform: rotateY(270deg) rotateX(0deg);
    }
    .cube-front {
      transform: rotateY(0deg) rotateX(0deg);
    }
    .cube-bottom {
      transform: rotateY(0deg) rotateX(90deg);
    }
    .cube-top {
      transform: rotateY(0deg) rotateX(270deg);
    }
    .cube {
      width: 100%;
      height: 100%;
      position: relative;
      transform-style: preserve-3d;
      transition: transform 1s;
      .cube__face {
        width: 8rem;
        height: 8rem;
        position: absolute;
        background: $background;
      }
      .cube__face--front {
        transform: rotateY(0deg);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        background: $background;
      }
      .cube__face--right {
        transform: rotateY(90deg);
        background: $base;
      }
      .cube__face--back {
        transform: rotateY(180deg);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        background: $background;
      }
      .cube__face--left {
        transform: rotateY(-90deg);
        background: $base;
      }
      .cube__face--top {
        transform: rotateX(90deg);
        background: $base;
      }
      .cube__face--bottom {
        transform: rotateX(-90deg);
        background: $base;
      }
      .cube__face--front {
        transform: rotateY(0deg) translateZ(4rem);
      }
      .cube__face--right {
        transform: rotateY(90deg) translateZ(4rem);
      }
      .cube__face--back {
        transform: rotateY(180deg) translateZ(4rem);
      }
      .cube__face--left {
        transform: rotateY(-90deg) translateZ(4rem);
      }
      .cube__face--top {
        transform: rotateX(90deg) translateZ(4rem);
      }
      .cube__face--bottom {
        transform: rotateX(-90deg) translateZ(4rem);
      }
      .cube.show-front {
        transform: translateZ(-4rem) rotateY(0deg);
      }
      .cube.show-right {
        transform: translateZ(-4rem) rotateY(-90deg);
      }
      .cube.show-back {
        transform: translateZ(-4rem) rotateY(-180deg);
      }
      .cube.show-left {
        transform: translateZ(-4rem) rotateY(90deg);
      }
      .cube.show-top {
        transform: translateZ(-4rem) rotateX(-90deg);
      }
      .cube.show-bottom {
        transform: translateZ(-4rem) rotateX(90deg);
      }
    }
  }
}